(function() {
  'use strict';

  angular
    .module('yoophawebapp')
    .directive('navBar', navBar);

  /** @ngInject */
  function navBar() {
    var directive = {
      restrict: 'E',
      templateUrl: 'app/components/navbar/navbar.html',
      bindToController: {
          searchInput: "=",
          navStyle: '@'
      },
      controller: NavbarController,
      controllerAs: 'nav',
      scope: true
    };

    return directive;

    /** @ngInject */
    function NavbarController($rootScope, $scope, $window, $timeout) {
      var vm = this;
      var scrollListener;
      var toolbarVisibilityCutoff;
      vm.showSearch = true;
      vm.subHeadedStyle = '';

      if($rootScope.currPage != 'searchResult') {
        vm.showSearch = false;
      } else {
        vm.showSearch = true;
      }

      // scrollListener = $scope.$watch(function(){
      //   return $window
      // })

      // toolbarVisibilityCutoff = 0.67 * $window.innerWidth - 50;
      $window.addEventListener('scroll', toggleToolbar, false);

      function toggleToolbar() {
        var pos = $window.scrollY;
        if($window.scrollY > toolbarVisibilityCutoff){
          // console.log('up goes',$window.scrollY, ':', toolbarVisibilityCutoff);
          if($window.scrollY > 38){
            // console.log('hide sub-header');
            $timeout(function() {
              vm.subHeadedStyle = 'hide-mini-header';
            }, 0);
          }
        }else{
          // console.log('down goes',$window.scrollY, ':', toolbarVisibilityCutoff);
          if($window.scrollY <=38) {
            // console.log('show sub-header');
            $timeout(function() {
              vm.subHeadedStyle = '';
            }, 0);
          }
        }
        toolbarVisibilityCutoff = pos;
        // if (changeUp && $window.scrollY > toolbarVisibilityCutoff) {
        //   changeUp = false;
        //   changeDown = true;
        //   $timeout(function() {
        //     vm.showToolbar = true;
        //   }, 0);
        // } else if (changeDown && $window.scrollY <= toolbarVisibilityCutoff) {
        //   changeUp = true;
        //   changeDown = false;
        //   $timeout(function() {
        //     vm.showToolbar = false;
        //   }, 0);
        // }
      }

      // console.log(vm.searchInput);

      // "vm.creationDate" is available by directive option "bindToController: true"
      // vm.relativeDate = moment(vm.creationDate).fromNow();
    }
  }

})();

