(function(angular) {
	'use strict';

	angular
		.module('yoophawebapp')
		.directive('chatHead', chatHead);

	/** @ngInject */
	function chatHead(){
		var directive = {
			restrict: 'E',
			templateUrl: 'app/components/chatHead/chatHead.html',
			bindToController: {
				name: "@"
			},
			controller: chatController,
      controllerAs: 'chat',
      scope: true,
      transclude: true
		};
		// console.log('chat directive');
		return directive;
		/** @ngInject */
    function chatController($scope /*, chatService*/) {
    	// console.log('chat controller');
      var vm = this; 
      vm.toggle = toggle;
      vm.currState = { status : 'closed', statusCode : 0, icon : 'fa-angle-up' };
      vm.usrMsg = '';
      // vm.send = send;
      vm.isClosed = false;
      vm.closeWindow = closeWindow;
      vm.data = {
      	from: 'user',
      	date: 'date',
      	msg: '',
      	chatName: '0321456987'
      };
      vm.chatHistory = [
      {from: 'user', date: '23-03-2017', time: '16:13', msg: 'this is a test message for the porpose of testing', chatName: "0321456987"},
      {from: 'master', date: '23-03-2017', time: '16:19', msg: 'this is a test message from the master', chatName: "0321456987"}];

    //   chatService.on('message', function (data) {
	   //    // console.log(data);
	   //    addConversation(data);
  	 //    // chatService.emit('user_msg', { my: 'data' });
  		// });

  		function addConversation(msg){
  			if(msg.from == 'master'){
  				msg.date = getDate();
  				msg.time = 'time';
  				vm.chatHistory.push(msg);
  			}else if(msg.from == 'user'){
  				// console.log(msg);
  				vm.chatHistory.push(msg);
  				vm.usrMsg = '';
  			}
  		}

      function toggle(){
      	// console.log('toggle started');
      	if(!vm.currState.statusCode) {
      		vm.currState.statusCode = 1;
      		vm.currState.status = 'opened';
      		vm.currState.icon = 'fa-angle-down'
      	} else {
      		vm.currState.statusCode = 0;
      		vm.currState.status = 'closed';
      		vm.currState.icon = 'fa-angle-up'
      	}
      }
      function closeWindow(flag){
      	if(flag){
      		vm.isClosed = true;
      	} else {
      		vm.isClosed = false;
      	}
      }

      function getDate() {
      	var d = new Date();
      	return d;
      }

      // function send(input) {
      // 	// console.log(input);
      // 	vm.data.msg = input;
      // 	vm.data.date = getDate();
      // 	chatService.emit('message', vm.data);
      // }
    }

	}
})(angular);
(function() {
  'use strict';

  angular
    .module('yoophawebapp')
    .factory('githubContributor', githubContributor);

  /** @ngInject */
  function githubContributor($log, $http) {
    var apiHost = 'https://api.github.com/repos/Swiip/generator-gulp-angular';

    var service = {
      apiHost: apiHost,
      getContributors: getContributors
    };

    return service;

    function getContributors(limit) {
      if (!limit) {
        limit = 30;
      }

      return $http.get(apiHost + '/contributors?per_page=' + limit)
        .then(getContributorsComplete)
        .catch(getContributorsFailed);

      function getContributorsComplete(response) {
        return response.data;
      }

      function getContributorsFailed(error) {
        $log.error('XHR Failed for getContributors.\n' + angular.toJson(error.data, true));
      }
    }
  }
})();

