(function(angular) {
  'use strict';

  /**
   * @ngdoc service
   * @name ipMobile.CityHome
   * @description
   * # CityHome
   * Service in the ipMobile.
   */
  angular.module('yoophawebapp')
    .service('uploadService', uploadService);

  /** @ngInject */
  function uploadService($q, $localStorage, getData, postData, utilities) {

    var service = {};
    service.createCart = createCart;

    function createCart(url, data){
      var deferred = $q.defer();
      postData.async(url, data)
        .then(function(res) {
          if(res.status == 201 && res.statusText == 'Created')
            deferred.resolve(res.data);
          else 
            deferred.reject(res.data);
        });
      return deferred.promise;
    }

    function getSearchData(url){
      var deferred = $q.defer();
      getData.async(url)
        .then(function(res) {
          deferred.resolve(res);
        });
      return deferred.promise;
    }

    return service;

  }
})(angular);
(function(angular) {
  'use strict';

  angular
    .module('yoophawebapp')
    .controller('uploadPrescriptionController', uploadPrescriptionController);

  /** @ngInject */
  function uploadPrescriptionController($scope, $http, $localStorage, $state, getData, uploadService) {
    var vm = this;
    var imageListener;
    var slides = vm.slides = [];
    var post_urls = ['http://ec2-35-154-138-58.ap-south-1.compute.amazonaws.com/api/v1/cart/create_cart/', 'http://ec2-35-154-138-58.ap-south-1.compute.amazonaws.com/api/v1/product/medicine_search/?search='];
    var currIndex = 0;
    var img = ['assets/images/img1.png', 'assets/images/img1.png', 'assets/images/img1.png', 'assets/images/img1.png', 'assets/images/img1.png', 'assets/images/img1.png'];
    vm.selectedImages = [];
    vm.selectedCartItem = [];
    vm.drug_key = undefined;
    vm.drug_search_result = [];
    vm.isImageSelected = false;
    vm.myInterval = 4500;
    vm.active = 0;
    vm.selected_drug = [];
    vm.prepUpload = prepUpload;
    vm.editSelected = editSelected;
    vm.routeTo = routeTo;
    vm.create_cart_presc = [];
    vm.search_drug = search_drug;
    vm.add_drug = add_drug;
    vm.del_selected_drug = del_selected_drug;
    vm.update_selected_drug_qty = update_selected_drug_qty;

    vm.addSlide = function() {
      var newWidth = 600 + slides.length + 1;
      slides.push({
        image: img[slides.length % 6],
        id: currIndex++
      });
    };

    init();



    function init(){
        var temp_data = $localStorage.selectedShop;
        if(!angular.isUndefined(temp_data)){
            vm.shop_detail = temp_data;
        } else {
            $state.go('Login');
        }
        // console.log(vm.shop_detail);
    	vm.shopDetails = $localStorage.selectedShop;
        for (var i = 0; i < 4; i++) {
          vm.addSlide();
        }
    }

    function search_drug(){
        if(vm.drug_key != '') {
            var url = post_urls[1] + vm.drug_key;
            getData.async(url)
            .then(function(res) {
                if(res.results.length > 0) {
                    vm.drug_search_result = res.results;
                } else {
                    vm.drug_search_result = [];
                }
            });
        }else {
            vm.drug_search_result = [];
        }
    }

    function del_selected_drug(item) {
        for(var i=0; i<vm.selected_drug.length; i++){
            var temp = vm.selected_drug[i];
            if(temp.product == item.product){
                vm.selected_drug.splice(i, 1);
            }
        }
    }

    function update_selected_drug_qty(action, item) {
        if(action && item.qty <= 100){
            item.qty++;
            item.temprice = item.price * item.qty;
        } else {
            if(item.qty <= 1){
                del_selected_drug(item);
            }else {
                item.qty--;
                item.temprice = item.temprice - item.price;
            }
        }
        prep_cart_item(item);
    }

    function add_drug(item){
        var index = id(vm.selected_drug, item, 0);
        if(index == -1) {
            item.qty = 1;
            item.temprice = item.price;
            vm.selected_drug.push(item);
            console.log(item);
            vm.drug_search_result = '';
            vm.drug_key = undefined;
            prep_cart_item(item);
        } else {
            vm.drug_search_result = '';
            vm.drug_key = undefined;
        }
    }

    function id(list, item, flag){
        if(list.length != 0){
            for(var i=0; i<list.length; i++){
                var temp_id = (flag) ? list[i].product : list[i].id;
                if(temp_id === item.id)
                    return i;
                else 
                    return -1;
            }
        } else {
            return -1;
        }
    }

    function prep_cart_item(item){
        var index = id(vm.selectedCartItem, item, 1);
        if(index == -1){
            var data = {
                // "cart": $localStorage['cart_id'].id,
                "product": item.id,
                "product_type": "1",
                // "name": item.name,
                "instruction": "Priscribed item",
                "schedule": "M",
                "quantity": item.qty,
                "size": undefined,
                "price": item.price
            };
            vm.selectedCartItem.push(data);
        } else {
            vm.selectedCartItem[index].quantity = item.qty;
        }
    }

    function prepUpload() {
    	var file = vm.selectedImages;
        var reader = new FileReader();
        reader.readAsDataURL(file[0]);
        reader.onload = function () {
          // console.log(reader.result);
          vm.create_cart_presc.push(
            {
                'user': $localStorage['usr'].usr_id,
                'image': reader.result
            });
        };
        reader.onerror = function (error) {
          console.log('Error: ', error);
        };
    }

    function editSelected(index){
    	vm.selectedImages.splice(index, 1);
        vm.create_cart_presc.splice(index, 1);
    }

    function routeTo(flag) {
        var data = {
            'pharmacy': $localStorage['selectedShop'].id,
            'user': $localStorage['usr'].usr_id,
            'prescription': vm.create_cart_presc,
            'tot_presc_amt': 0,
            'tot_otc_amount': 0,
            'total_amount': 0,
            'cartitems': vm.selectedCartItem,
            'active': true
        }
        create_cart(data, post_urls[0], true);

        $localStorage['selectedDrug'] = vm.selectedCartItem;
    	$state.go('OTC');
    }

    function create_cart_item(data, url, auth){
        $http({
            url : url,
            method : 'POST',
            data: data,
            headers : {
                  'Content-Type' : 'application/json',    
                  'Authorization': 'JWT ' + $localStorage['usr'].accessTok
                  }
            }).success(function(res){
                $localStorage['cart_id'].cart_item.push(res);
            }).error(function(err){
                console.log(err)
        })
    }

    function create_cart (data, url, auth) {
        if(angular.isUndefined($localStorage['cart_id'])) {
            uploadService.createCart(url, data)
                .then(function (res){
                    console.log(res);
                    $localStorage['cart_id'] = res;
                }, function(err){
                    alert(err);
                })
        }
    }

    imageListener = $scope.$watch(function () {
        return vm.selectedImages.length;
    }, function(newVal, oldVal) {
    	// console.log(newVal);
    	if(newVal === 0 || newVal === -1) {
    		vm.isImageSelected = false;
    	}else {
    		vm.isImageSelected = true;
    	}
    });
  }
})(angular);
