(function(angular) {
  'use strict';

  angular
    .module('yoophawebapp')
    .controller('searchResultController', searchResultController);

  /** @ngInject */
  function searchResultController($scope, $state, $localStorage, $window, $timeout, searchResult) {
    var vm = this;
    var img = ['/assets/images/img1.png', '/assets/images/img2.png', '/assets/images/img3.png'];
    var slides = vm.slides = [];
    var currIndex = 0;
    var searchApi = 'assets/JSON/searchResult.json';//'http://localhost:8080/api/v1/pharmacy/search_pharmacy/';
    var gridStyle = ['col-lg-6', 'col-lg-12'];
    var toolbarVisibilityCutoff;

    $scope.$on('$destroy', garbageCollection);
    vm.myInterval = 4500;
    vm.noWrapSlides = false;
    vm.active = 0;
    vm.searchData = [];
    vm.grid = 'col-lg-6';
    vm.filterStyle = 'rel-filter';
    vm.toggleStyle = toggleStyle;
    vm.routeTo = routeTo;

    $window.addEventListener('scroll', toggleToolbar, false);

    function toggleToolbar() {
      var pos = $window.scrollY;
      if($window.scrollY > toolbarVisibilityCutoff){
        // console.log('up goes',$window.scrollY, ':', toolbarVisibilityCutoff);
        //up scroll
        if($window.scrollY >= 309){
          $timeout(function() {
            vm.filterStyle = 'stick-filter';
          }, 0);
        }
      }else{
        if($window.scrollY <= 309){
          $timeout(function() {
            vm.filterStyle = 'rel-filter';
          }, 0);

        }
        // console.log('down goes',$window.scrollY, ':', toolbarVisibilityCutoff);
        //down scroll

      }
      // console.log(vm.filterStyle)
      toolbarVisibilityCutoff = pos;
    }

    vm.addSlide = function() {
      var newWidth = 600 + slides.length + 1;
      slides.push({
        image: img[slides.length % 3],
        id: currIndex++
      });
    };

    for (var i = 0; i < 4; i++) {
      vm.addSlide();
    }

    searchResult.getSearchData(searchApi)
    	.then(function(res){
    		// console.log(res);
        vm.searchData = res.results.docs;
    	});

    function toggleStyle(parm){
    	vm.grid = gridStyle[parm];
    }

    function routeTo(data){
    	$localStorage['selectedShop'] = data;
    	$state.go('uploadPrescription');
    }

    function garbageCollection() {
      $window.removeEventListener('scroll', toggleToolbar, false);
    }
  }
})(angular);
(function(angular) {
  'use strict';

  angular
    .module('yoophawebapp')
    .controller('RegisterController', RegisterController);

  /** @ngInject */
  function RegisterController($scope, $state, $localStorage, getData) {
    var vm = this;
    var reg_url = 'http://ec2-35-154-138-58.ap-south-1.compute.amazonaws.com/api/v1/auth/register/';
    // vm.clearText = clearText;
    vm.register_api = register_api;
    vm.reg = {
    	'username': '',
	  	'confirm_password': '',
	  	'password': '',
	  	'email': '',
	  	'mobile_no': ''
    };

    function clearText() {
		vm.reg = {
	    	'username': '',
		  	'confirm_password': '',
		  	'password': '',
		  	'email': '',
		  	'mobile_no': ''
    	};    	
    }

    function register_api() {
    	getData.relQuery(reg_url, vm.reg)
        .then(function(response) {
        	if(response.status == 201 && response.statusText == 'Created'){
	            clearText();
	            $state.go('Login');
        	} else {
        		console.log('something went wrong try again later');
        	}
      });
    }
    
  }
})(angular);
