(function(angular) {
  'use strict';


  angular.module('yoophawebapp')
    .service('homeService', homeService);

  /** @ngInject */
  function homeService($q, $http, $localStorage, getData, utilities) {

    var service = {};
    var gMapURL = 'https://maps.googleapis.com/maps/api/geocode/json?latlng=';
    var places = 'https://www.swiggy.com/api/place/autocomplete?input=';
    var pharma_search = 'http://ec2-35-154-138-58.ap-south-1.compute.amazonaws.com/api/v1/pharmacy/search_pharmacy/?search=';
    service.getLocation = getLocation;
    service.getTopCity = getTopCity;
    service.getAddressPrediction = getAddressPrediction;
    service.getPrediction = getPrediction;


    // function setPosition(position){
    //   console.log(position);
    // }

    // function showError(code){
    //   console.log(code);
    // }

    function getAddressPrediction(key){
      var deferred = $q.defer();
      var url = places + key;
      getData.async(url)
        .then(function(res) {
          // console.log(res);
          deferred.resolve(res);
        });
      return deferred.promise;
      // $http.jsonp(url)
      //   .success(function(data){
      //       var f = JSON.stringify(data)
      //       console.log(f);
      //       deferred.resolve(f);
      //   });
        // deferred.resolve('no data');
        // return deferred.promise;
      // return deferred.promise;
    }

    function getPrediction(key){
      var deferred = $q.defer();
      var url = pharma_search + key;
      getData.async(url)
        .then(function(res) {
          // console.log(res);
          deferred.resolve(res);
        });
      return deferred.promise;
    }    

    function getLocation() {
      var deferred = $q.defer();
      utilities.getLocation()
      .then(function(pos){
        var lat = pos.coords.latitude;
        var lng = pos.coords.longitude;
        $localStorage['lat_lng_data'] = {
          'lat' : lat,
          'lng' : lng,
        }
        var url = gMapURL + lat + ',' + lng
        getData.async(url)
          .then(function(res) {
            $localStorage['lat_lng_data'].address = res.results[0].formatted_address;
            var sArr = res.results[0].formatted_address.split(',');
            $localStorage['lat_lng_data'].deliveryLocation = sArr[sArr.length - 5].trim() + ',' + sArr[sArr.length - 4].trim();
            deferred.resolve(res);
          })
      }, function(err){
        deferred.reject(err);
      });
      return deferred.promise;
    }

    function getTopCity(url){
      var deferred = $q.defer();
      getData.async(url)
        .then(function(res) {
          deferred.resolve(res);
        });
      return deferred.promise;
    }

    return service;
  }
})(angular);
