(function(angular) {
  'use strict';

  /**
   * @ngdoc service
   * @name medArun.getData
   * @description
   * # getData
   * Factory in the medArun.
   */
  angular.module('yoophawebapp')
    .factory('getData', getData);

  /** @ngInject */
  function getData($http, $log) {
    var myService = {
      async: function (url) {
        // url = 'json/sample.json';
        var promise = $http.get(url).then(function (response) {
          return response.data;
        });

        return promise;
      },

      relQuery: function (url, sourceQuery) {
        var query = angular.copy(sourceQuery);
        // removeEmptyParams(query);
        $log.debug(angular.toJson(query));
        var promise = $http({
          method: 'POST',
          url: url,
          headers: {
            'Content-Type': 'application/json'
          },
          data: angular.toJson(query)
        }).then(function (response) {
          var output = angular.fromJson(angular.fromJson(response));
          // output.results.offset = query.offset;
          return output;
        });

        return promise;
      }
    };

    return myService;

    /**
     * Recursively removes empty parameters (objects||arrays) from container object
     * @param  {Object} obj Container object
     */
    function removeEmptyParams(obj) {
      var i;
      if (angular.isObject(obj)) {
        for (i in obj) {
          // because Object.keys(new Date()).length === 0;
          // we have to do some additional check
          if (Object.keys(obj[i]).length === 0 && (obj[i].constructor === Object || obj[i].constructor === Array)) {
            delete obj[i];
          } else {
            removeEmptyParams(obj[i]);
          }
        }
      }
    }
  }
})(angular);


(function(angular) {
  'use strict';

  angular
    .module('yoophawebapp')
    .controller('cartController', cartController);

  /** @ngInject */
  function cartController($localStorage, uploadService) {
    var vm = this;
    vm.otcDetail = [];
    vm.trash = trash;
    vm.changeQty = changeQty;
    vm.updateDrug = updateDrug;
    vm.createcart = createcart;

    init();

    function init(){
    	vm.otcDetail = $localStorage['selectedOTC'];
      vm.drugDetail = $localStorage['selectedDrug'];
    	// console.log(vm.otcDetail);
    }

    function updateDrug(item, flag){
      if(flag && item.quantity <= 100){
            item.quantity++;
            item.temprice = item.price * item.quantity;
        } else {
            if(item.quantity <= 1){
                del_selected_drug(item);
            }else {
                item.quantity--;
                item.temprice = item.temprice - item.price;
            }
        }
    }

    function del_selected_drug(item) {
      var index = id(vm.drugDetail, item);
      if(index != -1) {
        vm.drugDetail.splice(index, 1);
      }      
    }

    function id(list, item){
      if(list.length != 0){
            for(var i=0; i<list.length; i++){
                if(list[i].product === item.product)
                    return i;
                else 
                    return -1;
            }
        } else {
            return -1;
        }
    }

    function createcart(){
      var url = 'http://ec2-35-154-138-58.ap-south-1.compute.amazonaws.com/api/v1/cart/cart/create_cart';
      var data = {
        "total_amount": 0,
        "pharmacy": $localStorage['selectedShop'].id,
        "tot_presc_amt": 0,
        "tot_otc_amount": 0,
        "cartitems": vm.drugDetail,
        "active": true,
        "user": $localStorage['usr'].usr_id
      }
      uploadService.createCart(url, data)
          .then(function (res){
              console.log(res);
              $localStorage['cart_id'] = res;
          }, function(err){
              alert(err);
          })
    }

    function trash(data){
    	var index = hasID(vm.otcDetail, data);
    	vm.otcDetail.splice(index, 1);
    	$localStorage['selectedOTC'] = vm.otcDetail;
    }

    function changeQty(action, item){
    	var index = hasID(vm.otcDetail, item);
    	if(action) {
    	  item.qty++
    	}else {
    	  if(item.qty == 0 || item.qty == 1){
    	    item.qty = 0;
    	    item.isAdded = false;
    	  }else if(item.qty > 1) {
    	    item.qty--;
    	  }
    	}
    	vm.otcDetail[index] = item;
    	$localStorage['selectedOTC'] = vm.otcDetail;
    }

    function hasID(list, item){
      if(!angular.isUndefined(list)){
        for(var i=0; i < list.length; i++){
          var element = list[i];
          if(item.productid == element.productid)
            return i;
        }
      }
      return -1;
    }

  }
})(angular);
